/**
 * IndexRebuilder - 
 * 
 * 
 */

import fetchWithAuth from '@/utils/fetchWithAuth';
import BatchProcessor, { BatchOperationError } from '@/utils/batchProcessor';

/**
 * IndexRebuilder 
 * 
 * 
 */
class IndexRebuilder {
  /**
   *  IndexRebuilder 
   * @param {Object} options - 
   * @param {number} options.chunkSize - 
   * @param {number} options.maxRetries -  3
   * @param {number} options.retryDelay -  1000
   * @param {Function} options.onProgress - 
   * @param {Function} options.onError - 
   */
  constructor(options = {}) {
    this.chunkSize = options.chunkSize || null; // 
    this.maxRetries = options.maxRetries || 3;
    this.retryDelay = options.retryDelay || 1000;
    this.onProgress = options.onProgress || (() => {});
    this.onError = options.onError || (() => {});
    this.sessionId = this.generateSessionId();
    this.aborted = false;
  }

  /**
   * 
   * @returns {Promise<Object>}  { chunkSize, databaseType }
   */
  async fetchConfig() {
    try {
      const response = await fetchWithAuth('/api/manage/batch/index/config');
      if (!response.ok) {
        throw new Error('');
      }
      const result = await response.json();
      if (result.success) {
        return {
          chunkSize: result.chunkSize || 500,
          databaseType: result.databaseType || 'unknown'
        };
      }
      throw new Error(result.error || '');
    } catch (error) {
      //  D1
      console.warn('Failed to fetch index config, using default:', error);
      return { chunkSize: 500, databaseType: 'unknown' };
    }
  }

  /**
   * 
   * 
   * 
   * 1. 
   * 2. 
   * 3. 
   * 4. 
   * 5. 
   * 
   * @returns {Promise<Object>}  { success: boolean, totalFiles: number }
   * @throws {BatchOperationError} 
   */
  async rebuild() {
    this.aborted = false;
    
    try {
      // 
      if (!this.chunkSize) {
        const config = await this.fetchConfig();
        this.chunkSize = config.chunkSize;
      }

      // 
      this.onProgress({ 
        phase: 'fetching', 
        message: '...',
        current: 0
      });
      
      const processor = new BatchProcessor({
        onProgress: (p) => this.onProgress({ ...p, phase: 'fetching' }),
        onError: (e) => this.onError(e)
      });
      
      const records = await processor.fetchAllRecords(false);
      
      if (this.aborted) {
        throw new BatchOperationError('', 'ABORTED', false, '');
      }

      // 
      this.onProgress({ 
        phase: 'sorting', 
        message: '...',
        current: 0,
        total: records.length
      });
      
      this.sortByTimestampDescending(records);
      
      if (this.aborted) {
        throw new BatchOperationError('', 'ABORTED', false, '');
      }

      // 
      const chunks = this.splitIntoChunks(records);
      
      for (let i = 0; i < chunks.length; i++) {
        if (this.aborted) {
          throw new BatchOperationError('', 'ABORTED', false, '');
        }
        
        // 
        await this.uploadChunkWithRetry(chunks[i], i);
        
        this.onProgress({
          phase: 'uploading',
          current: i + 1,
          total: chunks.length,
          message: ` ${i + 1}/${chunks.length}...`
        });
      }

      // 
      this.onProgress({ 
        phase: 'finalizing', 
        message: '...',
        current: chunks.length,
        total: chunks.length
      });
      
      await this.finalize(chunks.length, records.length);

      this.onProgress({
        phase: 'completed',
        message: ` ${records.length} `,
        current: records.length,
        total: records.length
      });

      return { success: true, totalFiles: records.length };
    } catch (error) {
      const batchError = error instanceof BatchOperationError 
        ? error 
        : new BatchOperationError(
            error.message || '',
            'REBUILD_ERROR',
            true,
            ''
          );
      this.onError(batchError);
      throw batchError;
    }
  }

  /**
   * 
   * 
   * @param {Array} records - 
   */
  sortByTimestampDescending(records) {
    records.sort((a, b) => {
      const timestampA = (a.metadata && a.metadata.TimeStamp) || 0;
      const timestampB = (b.metadata && b.metadata.TimeStamp) || 0;
      return timestampB - timestampA;
    });
  }

  /**
   * 
   * 
   * @param {Array} records - 
   * @returns {Array<Array>} 
   */
  splitIntoChunks(records) {
    const chunks = [];
    for (let i = 0; i < records.length; i += this.chunkSize) {
      chunks.push(records.slice(i, i + this.chunkSize));
    }
    return chunks;
  }

  /**
   * 
   * 
   * @param {Array} chunk - 
   * @param {number} chunkId -  ID
   * @returns {Promise<Object>} 
   * @throws {BatchOperationError} 
   */
  async uploadChunkWithRetry(chunk, chunkId) {
    let lastError;
    
    for (let attempt = 1; attempt <= this.maxRetries; attempt++) {
      try {
        return await this.uploadChunk(chunk, chunkId);
      } catch (error) {
        lastError = error;
        
        // 
        if (error instanceof BatchOperationError) {
          if (error.code === 'AUTH_FAILED' || 
              error.code === 'FORBIDDEN' || 
              error.code === 'ABORTED') {
            throw error;
          }
        }
        
        // 
        if (attempt < this.maxRetries) {
          // 
          const delay = this.retryDelay * Math.pow(2, attempt - 1);
          await this.sleep(delay);
          
          this.onProgress({
            phase: 'retrying',
            message: ` ${chunkId + 1}  (${attempt}/${this.maxRetries})...`,
            current: chunkId,
            attempt: attempt
          });
        }
      }
    }
    
    // 
    throw lastError instanceof BatchOperationError 
      ? lastError 
      : new BatchOperationError(
          ` ${chunkId + 1}  ${this.maxRetries} `,
          'CHUNK_UPLOAD_FAILED',
          false,
          ''
        );
  }

  /**
   * 
   * 
   * @param {Array} chunk - 
   * @param {number} chunkId -  ID
   * @returns {Promise<Object>} 
   * @throws {BatchOperationError} 
   */
  async uploadChunk(chunk, chunkId) {
    const checksum = await this.calculateChecksum(chunk);
    
    try {
      const response = await fetchWithAuth('/api/manage/batch/index/chunk', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          chunkId: String(chunkId),
          sessionId: this.sessionId,
          data: chunk,
          checksum
        })
      });

      if (!response.ok) {
        let errorDetails = '';
        try {
          const errorData = await response.json();
          errorDetails = errorData.error || '';
        } catch {
          //  JSON 
        }
        throw this.createHttpError(response.status, errorDetails);
      }

      const result = await response.json();
      
      if (!result.success) {
        throw new BatchOperationError(
          result.error || '',
          'CHUNK_UPLOAD_FAILED',
          true,
          ''
        );
      }

      return result;
    } catch (error) {
      if (error instanceof BatchOperationError) {
        throw error;
      }
      // 
      if (error instanceof TypeError && error.message.includes('fetch')) {
        throw new BatchOperationError(
          '',
          'NETWORK_ERROR',
          true,
          ''
        );
      }
      throw new BatchOperationError(
        error.message || '',
        'CHUNK_UPLOAD_FAILED',
        true,
        ''
      );
    }
  }

  /**
   * 
   * 
   * @param {number} totalChunks - 
   * @param {number} totalFiles - 
   * @returns {Promise<Object>} 
   * @throws {BatchOperationError} 
   */
  async finalize(totalChunks, totalFiles) {
    try {
      const response = await fetchWithAuth('/api/manage/batch/index/finalize', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          sessionId: this.sessionId,
          totalChunks,
          totalFiles
        })
      });

      if (!response.ok) {
        let errorDetails = '';
        try {
          const errorData = await response.json();
          errorDetails = errorData.error || '';
        } catch {
          //  JSON 
        }
        throw this.createHttpError(response.status, errorDetails);
      }

      const result = await response.json();
      
      if (!result.success) {
        throw new BatchOperationError(
          result.error || '',
          'FINALIZE_FAILED',
          true,
          ''
        );
      }

      return result;
    } catch (error) {
      if (error instanceof BatchOperationError) {
        throw error;
      }
      throw new BatchOperationError(
        error.message || '',
        'FINALIZE_FAILED',
        true,
        ''
      );
    }
  }

  /**
   *  ID
   * 
   * @returns {string}  ID
   */
  generateSessionId() {
    return `rebuild_${Date.now()}_${Math.random().toString(36).slice(2)}`;
  }

  /**
   *  SHA-256 
   * 
   * @param {Array} data - 
   * @returns {Promise<string>} 
   */
  async calculateChecksum(data) {
    const text = JSON.stringify(data);
    const encoder = new TextEncoder();
    const dataBuffer = encoder.encode(text);
    const hashBuffer = await crypto.subtle.digest('SHA-256', dataBuffer);
    const hashArray = Array.from(new Uint8Array(hashBuffer));
    return hashArray.map(b => b.toString(16).padStart(2, '0')).join('');
  }

  /**
   *  HTTP 
   * 
   * @param {number} status - HTTP 
   * @param {string} details - 
   * @returns {BatchOperationError}
   */
  createHttpError(status, details = '') {
    switch (status) {
      case 401:
        return new BatchOperationError(
          '',
          'AUTH_FAILED',
          false,
          ''
        );
      case 403:
        return new BatchOperationError(
          '',
          'FORBIDDEN',
          false,
          ''
        );
      case 400:
        return new BatchOperationError(
          `: ${details}`,
          'INVALID_DATA',
          true,
          ''
        );
      case 404:
        return new BatchOperationError(
          '',
          'SESSION_NOT_FOUND',
          false,
          ''
        );
      case 410:
        return new BatchOperationError(
          '',
          'SESSION_EXPIRED',
          false,
          ''
        );
      case 500:
      default:
        return new BatchOperationError(
          `: ${details || status}`,
          'SERVER_ERROR',
          true,
          ''
        );
    }
  }

  /**
   * 
   * 
   * @param {number} ms - 
   * @returns {Promise<void>}
   */
  sleep(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
  }

  /**
   * 
   */
  abort() {
    this.aborted = true;
  }

  /**
   * 
   * 
   */
  reset() {
    this.aborted = false;
    this.sessionId = this.generateSessionId();
  }
}

export default IndexRebuilder;
